}
.header .header__container .header__burger {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.header .header__container .header__burger .header__burger-line {
  border-bottom: 4px solid #ffffff;
  border-radius: 4px;
  width: 30px;
}
