  margin-left: 142px;
}
.header .header__container .logo:hover img {
  content: url(../../images/common/logo.hover.png);
}
.header .header__container #right {
  display: none;
  width: 30px;
  height: 28px;
}
.header .header__container .header__menu {
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  flex-direction: column;
  align-items: start;
  padding-top: 35px;
  padding-bottom: 236px;
  background-color: #25252d;
  width: 252px;
  box-sizing: border-box;
}
.header .header__container .header__menu::before {
  content: "";
