  top: 0;
  left: -100vw;
  width: 100vw;
  height: 820px;
  background-color: #25252d;
}
.header .header__container .header__menu .header__cross {
  width: 30px;
  height: 30px;
  position: relative;
  top: 19px;
  left: 0;
  transform: translateY(-50%);
