}
.header .header__container .header__menu .header__cross .header__cross-line {
  position: absolute;
  width: 41px;
  height: 3px;
  border-radius: 3px;
  background-color: #ffffff;
  top: 50%;
  left: 50%;
  transform-origin: center;
}
.header .header__container .header__menu .header__cross .header__cross-line-1 {
  transform: translate(-50%, -50%) rotate(45deg);
}
.header .header__container .header__menu .header__cross .header__cross-line-2 {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.header .header__container .header__menu .header__title {
