$white-color: #ffffff;
$orange-color: #ecc66b;
$dark-color: #25252d;

@mixin center-circle {
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

@mixin h2 {
    font-size: 40px;
    font-family: "Neumann", sans-serif;
    color: #ecc66b;
    text-align: center;
}

@mixin button {
    background-image: -moz-linear-gradient(0deg, #fcc372 0%, #f0dbbc 100%);
    background-image: -webkit-linear-gradient(0deg, #fcc372 0%, #f0dbbc 100%);
    background-image: -ms-linear-gradient(0deg, #fcc372 0%, #f0dbbc 100%);
    background-image: linear-gradient(0deg, #fcc372 0%, #f0dbbc 100%);
    font-size: 18px;
    color: $dark-color;
    border: 0;
    box-sizing: border-box;
    font-family: "Museosanscyrl_700", sans-serif;
}

@mixin for_projects_135 {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
    max-width: 588px;
}

@mixin img_first-child {
    width: 588px;
    height: 370px;
}

@mixin img_other-child {
    width: 180px;
    height: 135px;
}

@mixin section_h3 {
    font-size: 30px;
    font-family: "Museosanscyrl_500", sans-serif;
    color: $white-color;
    margin-bottom: 32px;
}

@mixin section_p {
    font-size: 18px;
    font-family: "Museosanscyrl_300", sans-serif;
    color: $white-color;
    line-height: 1.278;
    margin-bottom: 47px;
}

@mixin section_circles {
    max-width: 486px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
    font-size: 20px;
    line-height: 1.25;
    font-family: "MuseoSansCyrl_500", sans-serif;
    text-align: center;
}

@mixin section_circl {
    width: 110px;
    height: 110px;
    border: 1px solid $orange-color;
    border-radius: 50%;
    box-sizing: border-box;
}

@mixin section_button {
    border-radius: 30px;
    width: 365px;
    height: 60px;
}

* {
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

@font-face {
    font-family: "Museosanscyrl_700";
    src: url('../../fonts/Museo_Sans_Cyrl_700.ttf');
}

@font-face {
    font-family: "Neumann";
    src: url('../../fonts/Neumann.ttf');
}

@font-face {
    font-family: "Museosanscyrl_500";
    src: url('../../fonts/Museo_Sans_Cyrl_500.otf');
}

@font-face {
    font-family: "Museosanscyrl_300";
    src: url('../../fonts/Museo_Sans_Cyrl_300.ttf');
}

@font-face {
    font-family: "Museosanscyrl_100";
    src: url('../../fonts/Museo_Sans_Cyrl_100.ttf');
}

.background {
    background:
        linear-gradient(90deg, $dark-color 0%, $dark-color 100%),
        /* Стандартный градиент */
        url('../../images/main_block/background.png') no-repeat center;
    /* Фоновая картинка */
    background-size: cover;
    background-blend-mode: overlay;
}

body {
    font-family: "Museosanscyrl_700", sans-serif;
    background-color: $dark-color;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    // overflow: hidden;
    box-sizing: border-box;
}

a {
    cursor: pointer;
    text-decoration: none;
}

.header {
    border-bottom: 1px solid #363d51;
    position: relative;
    z-index: 1;

    .header__container {
        padding: 20px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;

        .header__burger {
            display: flex;
            flex-direction: column;
            gap: 8px;

            .header__burger-line {
                border-bottom: 4px solid $white-color;
                border-radius: 4px;
                width: 30px;
            }
        }

        .logo {
            margin-left: 142px;

            // img {
            //     transition: all 1s ease;
            // }

            &:hover {
                img {
                    content: url(../../images/common/logo.hover.png);
                }
            }
        }

        #right {
            display: none;
            width: 30px;
            height: 28px;
        }

        .header__menu {
            position: absolute;
            top: 0;
            left: 0;
            display: none;
            flex-direction: column;
            align-items: start;
            padding-top: 35px;
            padding-bottom: 236px;
            background-color: $dark-color;
            width: 252px;
            box-sizing: border-box;
            // z-index: 10;

            &::before {
                content: "";
                position: absolute;
                top: 0;
                left: -100vw;
                width: 100vw;
                height: 820px;
                background-color: $dark-color;

            }


            .header__cross {
                width: 30px;
                height: 30px;
                position: relative;
                top: 19px;
                left: 0;
                transform: translateY(-50%);
                margin-bottom: 52px;

                .header__cross-line {
                    position: absolute;
                    width: 41px;
                    height: 3px;
                    border-radius: 3px;
                    background-color: $white-color;
                    top: 50%;
                    left: 50%;
                    transform-origin: center;
                }

                .header__cross-line-1 {
                    transform: translate(-50%, -50%) rotate(45deg);
                }

                .header__cross-line-2 {
                    transform: translate(-50%, -50%) rotate(-45deg);
                }
            }

            .header__title {
                font-size: 40px;
                font-family: "Neumann", sans-serif;
                color: $white-color;
                margin-bottom: 61px;
            }

            nav {
                margin-bottom: 71px;

                .header__menu-items {
                    display: flex;
                    flex-direction: column;
                    gap: 30px;
                    font-size: 18px;
                    font-family: "Museosanscyrl_300", sans-serif;
                    color: $white-color;
                    list-style: none;

                    .header__menu-item {
                        font-family: "Museosanscyrl_300", sans-serif;
                        color: $white-color;
                        font-size: 18px;
                        display: flex;
                        align-items: center;

                        &:hover {
                            .header__menu-item-circle {
                                display: block;
                            }
                        }

                        &:hover {
                            color: $orange-color;
                        }


                        .header__menu-item-circle {
                            display: none;
                            width: 5px;
                            height: 5px;
                            background-color: $orange-color;
                            border-radius: 50%;
                            margin-right: 11px;
                        }
                    }
                }
            }

            .icons {

                a:first-child {
                    position: absolute;
                    top: 588px;
                    left: 0;

                    &:hover {
                        content: url(../../images/common/instagram.hover.png);
                    }
                }

                a:nth-child(2) {
                    position: absolute;
                    top: 590px;
                    left: 60px;

                    &:hover {
                        content: url(../../images/common/telegram.hover.png);
                    }
                }

                a:last-child {
                    position: absolute;
                    top: 591px;
                    left: 130px;

                    &:hover {
                        content: url(../../images/common/youtube.hover.png);
                    }
                }
            }
        }

    }
}

.info--block {
    display: none;
}

.info {
    display: flex;
    flex-direction: column;
    gap: 13px;

    .info__phone {
        font-size: 18px;
        color: $white-color;

        &:hover {
            color: $orange-color;
        }
    }

    .info__call {
        display: flex;
        align-items: center;
        justify-content: end;

        .info__call-circle {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: 8px;

            .info__call-circle-3 {
                border: 1px solid $white-color;
                width: 31px;
                height: 31px;
                @include center-circle();

                .info__call-circle-2 {
                    border: 1px solid $white-color;
                    width: 19px;
                    height: 19px;
                    @include center-circle();


                    .info__call-circle-1 {
                        background-color: $white-color;
                        width: 7px;
                        height: 7px;
                        border-radius: 50%;

                    }
                }
            }

        }

        .info__order-a-call {
            font-size: 16px;
            color: $white-color;
        }

        &:hover {
            .info__order-a-call {
                color: $orange-color;
            }

            .info__call-circle-3,
            .info__call-circle-3 .info__call-circle-2 {
                border-color: $orange-color;
            }

            .info__call-circle-3 .info__call-circle-2 .info__call-circle-1 {
                background-color: $orange-color;
            }
        }
    }
}

// ----------------------------MAIN----------------------------
.main {

    .main__container {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding-top: 161px;
        padding-bottom: 275px;
        text-align: center;

        h1 {
            margin-bottom: 33px;
            font-size: 50px;
            font-family: "Neumann", sans-serif;
            color: $orange-color;
            line-height: 1.2;
            max-width: 552px;
        }

        p {
            margin-bottom: 41px;
            font-size: 18px;
            color: $white-color;
        }

        button {
            width: 300px;
            height: 60px;
            padding: 21px;
            border-radius: 30px;
            @include button;
        }


        .main__social-icons {
            display: flex;
            flex-direction: column;

            a:first-child {
                position: absolute;
                top: 203px;
                right: 0;

                &:hover {
                    content: url(../../images/common/instagram.hover.png);
                }
            }

            a:nth-child(2) {
                position: absolute;
                top: 263px;
                right: 0;

                &:hover {
                    content: url(../../images/common/telegram.hover.png);
                }
            }

            a:last-child {
                position: absolute;
                top: 319px;
                right: 0;

                &:hover {
                    content: url(../../images/common/youtube.hover.png);
                }
            }
        }

        .main__down-arrow {
            position: absolute;
            transform: rotate(-90deg);
            top: 370px;
            left: -86px;

            img {
                height: 15px;
                width: 30px;
            }

            span {
                font-size: 18px;
                font-family: "Museosanscyrl_500", sans-serif;
                color: #bababa;
                margin-left: 30px;
            }
        }
    }
}

// ----------------------------SECTION----------------------------
.section {
    .section__container {
        padding-top: 107px;
        padding-bottom: 60px;

        h2 {
            @include h2();
            margin-bottom: 69px;
        }

        .section__projects {
            display: flex;
            flex-direction: column;
            gap: 119px;

            .section__projects--135 {
                display: flex;
                gap: 126px;

                .section__photo {
                    @include for_projects_135;

                    img:first-child {
                        @include img_first-child;
                    }


                    img:nth-child(2) {
                        @include img_other-child;
                    }


                    img:nth-child(3) {
                        @include img_other-child;
                    }

                    img:last-child {
                        @include img_other-child;
                    }
                }

                .section__text {
                    display: flex;
                    flex-direction: column;

                    h3 {
                        @include section_h3;
                    }

                    p {
                        @include section_p;
                    }

                    .section__text--circles {
                        @include section_circles;
                        color: $orange-color;

                        div:first-child {
                            @include section_circl;
                            padding-top: 40px;
                        }

                        div:nth-child(2) {
                            @include section_circl;
                            padding-top: 27px;
                        }

                        div:last-child {
                            @include section_circl;
                            padding-top: 27px;
                        }
                    }

                    .button--yellow {
                        @include section_button;
                        @include button;
                        margin-bottom: 20px;

                    }

                    .button--transparent {
                        border: 1px solid $white-color;
                        @include section_button;
                        font-size: 18px;
                        color: $white-color;
                        background-color: transparent;
                    }
                }
            }

            .section__projects--24 {
                display: flex;
                flex-direction: row-reverse;
                gap: 114px;


                .section__photo {
                    display: flex;
                    flex-wrap: wrap;
                    gap: 24px;
                    max-width: 588px;

                    img:first-child {
                        @include img_first-child;
                    }

                    img:nth-child(2) {
                        @include img_other-child;
                    }

                    img:nth-child(3) {
                        @include img_other-child;
                    }

                    img:last-child {
                        @include img_other-child;
                    }
                }

                .section__text {
                    display: flex;
                    flex-direction: column;

                    h3 {
                        @include section_h3;
                    }


                    p {
                        @include section_p;
                    }


                    .section__text--circles {
                        @include section_circles;
                        color: $orange-color;


                        div:first-child {
                            @include section_circl;
                            padding-top: 40px;
                        }

                        div:nth-child(2) {
                            @include section_circl;
                            padding-top: 27px;
                        }

                        div:last-child {
                            @include section_circl;
                            padding-top: 27px;
                        }
                    }

                    .button--yellow {
                        @include section_button;
                        @include button;
                        margin-bottom: 20px;

                    }

                    .button--transparent {
                        border: 1px solid $white-color;
                        @include section_button;

                        font-size: 18px;
                        color: $white-color;
                        background-color: transparent;
                    }
                }
            }

        }

        .section_see-more {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-left: auto;
            margin-right: auto;
            margin-top: 70px;
            border: 0;
            background-color: transparent;

            &:hover {
                #section_see-more-text {
                    color: $orange-color;
                }
            }

            &:hover {
                #check {
                    content: url(../../images/section/check_hover.png);
                }
            }

            #section_see-more-text {
                font-size: 18px;
                font-family: "Museosanscyrl_500", sans-serif;
                color: #89898a;
                margin-right: 11px;
            }

        }
    }
}

.section_h3-block {
